import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import Helmet from "react-helmet";
import Loader from "../../Components/Loader";

const Container = styled.div`
  height: calc(100vh - 50px);
  width: 100%;
  padding: 50px;
`;

const Content = styled.div`
  display: flex;
  width: 100%;
  position: relative;
  z-index: 1;
  height: 100%;
`;

const Cover = styled.div`
  width: 30%;
  height: 100%;
  background-image: url(${props => props.bgImage});
  background-position: center;
  background-size: cover;
  border-radius: 5px;
`;

const Backdrop = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(${props => props.bgImage});
  background-position: center;
  background-size: cover;
  filter: blur(3px);
  opacity: 0.5;
  z-index: 0;
`;

const Data = styled.div`
  width: 70%;
  margin-left: 10px;
`;

const Title = styled.h3`
  font-size:34px;
  margin-bottom: 10px;
`;

const ItemContainer = styled.div`
  margin-bottom: 20px;
`;

const Item = styled.span`

`;

const Divider = styled.span`
  margin: 0 10px;
`;

const Overview = styled.p`
  font-size: 12px;
  color: white;
  opacity: 0.7;
  line-height: 1.5;
  width: 50%;
`;

const HomePresenter = ({result, loading, error}) => 
loading ? (
  <>
    <Helmet>Loading... | Nomflix</Helmet>
    <Loader />
  </>
) : (
  <>
  <Helmet>
    <title>{result.original_title ? result.original_title : result.original_name} | Nomflix</title>
  </Helmet>
  <Container>
    <Backdrop bgImage={`https://image.tmdb.org/t/p/original${result.backdrop_path}`} />
    <Content>
      <Cover bgImage={result.poster_path ? `https://image.tmdb.org/t/p/original${result.poster_path}` : require("../../assets/noPosterImage.png")} />
      <Data>

        <Title>{result.original_title ? result.original_title : result.original_name}</Title>

        <ItemContainer>
          <Item>{result.release_date ? result.release_date.substring(0, 4) : result.first_air_date.substring(0, 4)}</Item>
          <Divider>üç©</Divider>
          <Item>{result.runtime ? result.runtime : result.episode_run_time[0]} min</Item>
          <Divider>üç©</Divider>
          <Item>{result.genres && result.genres.map((genre, index) => index === result.genres.length - 1 ? genre.name : `${genre.name} / ` )}</Item>
        </ItemContainer>

        <Overview>
          {result.overview}
        </Overview>

      </Data>
    </Content>
  </Container>
  </>
)
;

HomePresenter.propTypes = {
  result:PropTypes.object,
  loading:PropTypes.bool.isRequired,
  error:PropTypes.string
}

export default HomePresenter;