import React from "react";
import { BrowserRouter as Router, Route, Redirect, Switch} from "react-router-dom";
import Home from "../Routes/Home"
import TV from "../Routes/TV"
import Detail from "../Routes/Detail"
import Search from "../Routes/Search"
import Header from "./Header";

const Routers = () => (
  <Router>
    <>
      <Header />
      <Switch>
        <Route path="https://mankyung.github.io/nomflix/" exact component={Home} />
        <Route path="https://mankyung.github.io/nomflix/tv" exact component={TV} />
        <Route path="https://mankyung.github.io/nomflix/search" component={Search} />
        <Route path="https://mankyung.github.io/nomflix/movie/:id" component={Detail} />
        <Route path="https://mankyung.github.io/nomflix/show/:id" component={Detail} />
        <Redirect from="*" to="/" />
      </Switch>
    </>
  </Router>
)

export default Routers;